                scrollTop: null,

                // 定义按钮默认状态
                isScrollTop: false,

                logoChanged: false,
                logoTop: 140,
                logoLeft: 72,
                logoPosition: 'fixed',
                logoScale: 1,
                xx: ['','','','',''],
                logoColor: '#fff',
                filinkScale: 1,
                filinkTranslate: '0,0',
                defiTop: 260,
                imgsrc:'000.png',
                imgCount: 1,
                basicParams:[
                    [0.05,6,1000,100],
                    [0.05,-3,1050,130],
                    [0.05,11,1130,190],
                    [0.05,-17,1250,50],
                    [0.05,22,1350,30],
                    [0.05,-9,1500,40],
                    [0.05,6,1600,140],
                    [0.05,-16,1730,110],
                    [0.05,30,1810,30],
                ],
                params:[
                    [0.05,6,1000,100],
                    [0.05,-3,1050,130],
                    [0.05,11,1130,190],
                    [0.05,-17,1250,50],
                    [0.05,22,1350,30],
                    [0.05,-9,1500,40],
                    [0.05,6,1600,140],
                    [0.05,-16,1730,110],
                    [0.05,30,1810,30],
                ]
            }
        },
        props: {
            el: String
        },
        mounted() {
            // 监听滚动事件
            window.addEventListener('scroll', () => {
                    this.movingBars()

                this.scrollTop = document.documentElement&&document.documentElement.scrollTop ||
                    window.pageYOffset ||
                    document.body&&document.body.scrollTop ||
                    document.querySelector(this.el)&&document.querySelector(this.el).scrollTop || 0;
                    

            }, true);

            if(document.documentElement.offsetWidth < 420){
                //
                this.logoTop = 18;
                this.logoLeft = 18;
                this.logoScale = 0.17;
                this.logoColor = '#505050';
                this.filinkScale = 0.55;
                this.filinkTranslate = '-70px,-24px';
                this.defiTop = 120;
            }

            this.imgPreload()

            this.movingBars()

        },
        methods:{
            movingBars(){
                    
                for(let i=0;i<this.basicParams.length;i++){
                    let a = Math.random()*0.05
                    this.params[i][0] = this.basicParams[i][0] + a
                    let b = Math.floor(Math.random()*10 - 5)
                    this.params[i][1] = this.basicParams[i][1] + b
                    let c = Math.floor(Math.random()*50 - 25)
                    this.params[i][2] = this.basicParams[i][2] + c
                    let d = Math.floor(Math.random()*40 - 20)
                    this.params[i][3] = this.basicParams[i][3] + d
                }
            },
            imgPreload(){
                let img = new Image()
                img.src = 'resources/'+('00'+this.imgCount).substr(-3)+'.jpg'
                img.onload = ()=>{
                    this.imgsrc = ('00'+this.imgCount).substr(-3)+'.jpg'
                    // eslint-disable-next-line no-console
                    console.log(this.imgsrc)
                    setTimeout(()=>{
                        if(this.imgCount<11){
                            this.imgCount++
                            this.imgPreload()
                        } else {
                            this.imgCount = 1
                            this.slideShow()
                        }
                    },6000)
                    
                }
            },
            slideShow(){
                this.imgsrc = ('00'+this.imgCount).substr(-3)+'.jpg'
                setTimeout(()=>{
                    if(this.imgCount<11){
                        this.imgCount++
                    } else {
                        this.imgCount = 1
                    }
                    this.slideShow()
                },6000)
            },
        },
        watch:{
            scrollTop(){
                if(document.documentElement.offsetWidth < 420) return;
                // eslint-disable-next-line no-console
                if(this.scrollTop > 70){

                    if(!this.logoChanged){

                        this.logoTop = 20;
                        this.logoScale = 0.2;
                        this.logoColor = '#505050';
                        this.filinkScale = 0.75;
                        this.filinkTranslate = '0,-12px';

                        this.logoChanged = true;
                    }

                } else {

                    if(this.logoChanged){

                        //
                        this.logoTop = 140;
                        this.logoScale = 1;
                        this.logoColor = '#fff';
                        this.filinkScale = 1;
                        this.filinkTranslate = '0,0';

                        this.logoChanged = false;
                    }
                }
            }
        }
    }
</script>

